// 游戏固定参数设定
export default class Setting {
    // 基本常量定义
    public static get Game_Column() { return 4; }
    public static get Game_Row() { return 15; }
    public static get Game_Row2() { return 30; }
    public static get Cube_width() { return 15; }
    public static get Cube_Height() { return 30; }
    // 网格指针
    protected static _GridCurrentPoint: number = 0;
    public static get GridCurrentPoint() { return this._GridCurrentPoint }
    public static set GridCurrentPoint(value) { this._GridCurrentPoint = value }
    public static get GridPointer() { return this._GridCurrentPoint++ }
    public static set GridPointer(value) { this._GridCurrentPoint += value }

    // 
}